.App {
margin: 0px;
}
main{
  padding: 36px;
  max-width:100%;
}
nav{
  display: flex;
  flex-direction: row;
  text-align: center;
  max-width: 100%;
  height: 65px;
  padding :15px;
  
  color: #FFFFFF;  
  background: linear-gradient(90deg, #672280 1.18%, #7F249F 39.06%, #A626D3 100%);
}
.nav--logTitle{
 margin-right:auto;
font-family: 'Karla';
font-style: normal;
font-size: 1.25rem;
}
.nav--logoImg{
height: 100%;
margin-right: 6px;
}
.nav--project{
  font-size: 0.75rem;
  margin-right: 10px;
}
.form{
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
  max-width:100%;
}
.form--input{
  font-family: 'Karla';
  border-radius: 5px; 
 border: 1px solid #D5D4D8;
 text-indent: 5px;
}
.form--button{
  grid-column: 1 / -1;
  font-family: 'Karla';
  border-radius: 5px;
  background: linear-gradient(90deg, #672280 1.18%, #7F249F 39.06%, #A626D3 100%);
  color: white;
  border: none;
  cursor: pointer;
  
}
.Image-container {
  position: relative;
  justify-content: center;
  color: white;
  left: 50%;
transform: translateX(-50%);
}
.main-image{
  max-width: 100%;
  margin-top: 30px;
  
}
.top-left {
  background-color: rgba(0, 0, 0, 0.651);
  position: absolute;
  text-align: center;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  font-size: xx-large;
  max-width: max-content;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.bottom-left {
  background-color: rgba(0, 0, 0, 0.651);
  position: absolute;
  text-align: center;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  font-size: xx-large;
  max-width: max-content;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}